body {
  background: #9266ff;
}

.app {
  background: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#todo-form input {
  background: rgb(27, 112, 137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
}
#todo-form input::placeholder {
  color: rgba(255, 255, 255, 0.75);
}

#todo-form input:focus,
#todo-form button:focus {
  outline: none;
}

#todo-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background: rgb(202, 156, 24);
  cursor: pointer;
  font-weight: bolder;
  color: white;
}
